.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #4b48db;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  height: 19px;
  width: 19px;
  background-color: #4b48db;
  transition: all .4s;
  position: absolute;
  bottom: 0;
  left: 1px;
}

input:checked + .slider {
  background-color: #4440f7;
  border: 1px solid #fff;
}

input:focus + .slider {
  box-shadow: 0 0 1px #4b48db;
}

input:checked + .slider:before {
  background-color: #fff;
  transform: translateX(18px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

