summary:after {
  color: #0000;
  width: 15px;
  height: 25px;
  content: "";
  background: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCIgdmlld0JveD0iMCAwIDI0IDI0Ij48cGF0aCBkPSJNMTYuNTkgOC41OUwxMiAxMy4xNyA3LjQxIDguNTkgNiAxMGw2IDYgNi02eiIvPjxwYXRoIGQ9Ik0wIDBoMjR2MjRIMHoiIGZpbGw9Im5vbmUiLz48L3N2Zz4=") 50% / 1em 1em no-repeat;
  margin-left: 8px;
  transition: transform .5s;
}

details[open] > summary:after {
  transform: scale(1, -1);
}

summary ~ * {
  padding: 0 1em 10px 1.4em;
}

